.loader-points {
    background-color: #4dcc5c;
    padding: 40px 20px;
    color: #fff;
  }
  
  .loader-points-container {
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .loader-points-row {
    display: flex;
    margin-bottom: 30px;
    flex-wrap: wrap;
  }
  
  .loader-point {
    display: flex;
    align-items: flex-start;
    width: 50%;
    margin-bottom: 15px;
  }
  
  .point-number {
    background-color: #000;
    color: #4dcc5c;
    border-radius: 50%;
    width: 45px;
    height: 45px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    font-weight: 800;
    margin-right: 15px;
    flex-shrink: 0;
  }
  
  .point-content {
    flex: 1;
  }
  
  .point-content h3 {
    font-family: 'Poppins', sans-serif;
    font-weight: 800;
    font-size: 16px;
    margin: 0 0 8px 0;
    text-transform: uppercase;
  }
  
  .point-content p {
    font-family: 'Poppins', sans-serif;
    font-size: 14px;
    line-height: 1.4;
    margin: 0;
  }
  
  .point-content strong {
    font-weight: bold;
  }
  
  /* Media query for mobile devices */
  @media (max-width: 768px) {
    .loader-point {
      width: 100%;
    }
    
    .loader-points-row {
      flex-direction: column;
    }
  }